body {
    margin: 0;
    font-family: "Segoe UI", sans-serif;
    background: #f2f5f9;
    color: #333;
  }
  
  .container {
    max-width: 900px;
    margin: auto;
    padding: 2rem;
  }
  
  .header {
    text-align: center;
    margin-bottom: 2rem;
  }
  
  button.download-btn {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 12px 24px;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    margin-top: 1rem;
  }
  
  .download-btn:hover {
    background-color: #0056b3;
  }
  